import { Component, Host, h, Prop } from '@stencil/core';
import { Memoize, parseIfString } from '@springbokagency/stencil-base';
import { BsArticleCard } from '../../molecules/article-card/article-card';
import { BsTitle } from '../../atoms/title/title';

/**
 * Related articles component.
 */
@Component({
  tag: 'bs-related-articles',
  styleUrl: 'related-articles.scss',
  shadow: true,
})
export class BsRelatedArticlesCard {
  /**
   * The article related image.
   */
  @Prop() articlesData: BsArticleCard[] | string;

  @Memoize('articlesData')
  get parsedArticlesData(): BsArticleCard[] {
    return parseIfString(this.articlesData);
  }

  /**
   * Description.
   */
  @Prop() relatedArticlesTitle: BsTitle | string;

  @Memoize('relatedArticlesTitle')
  get parsedRelatedArticlesTitle(): BsTitle {
    return parseIfString(this.relatedArticlesTitle);
  }

  render() {
    return (
      <Host>
        <bs-title
          text={this.parsedRelatedArticlesTitle.text}
          tag={this.parsedRelatedArticlesTitle.tag}
          size={this.parsedRelatedArticlesTitle.size}
          variant={this.parsedRelatedArticlesTitle.variant}
        />
        <div class="related-articles">
          {this.parsedArticlesData.map((article) => (
            <bs-article-card
              articleTitle={article.articleTitle}
              articleCta={article.articleCta}
              articleHref={article.articleHref}
              articleImage={article.articleImage}
            />
          ))}
        </div>
      </Host>
    );
  }
}
